.rc-dialog-wrap {
}

.rc-dialog {
  outline: none;
  position: absolute;
  left:-9999px;
  top:-9999px;
  z-index: 1000;
}

.rc-dialog-title {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 18px;
}

.rc-dialog-content {
  box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
  position: relative;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 6px;
  outline: 0;
}

.rc-dialog-hidden,
.rc-dialog-mask-hidden {
  display: none;
}

.rc-dialog-close {
  cursor: pointer;
  outline: none;
  margin-top: -2px;
  float: right;
  font-size: 21px;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  filter: alpha(opacity=20);
  opacity: .2;
  text-decoration: none;

  &-x:after {
    content:'×'
  }

  &:hover {
    opacity: 1;
    filter: alpha(opacity=100);
    text-decoration: none;
  }
}

.rc-dialog-header {
  min-height: 16.43px;
  padding: 15px;
  border-bottom: 1px solid #e5e5e5;
}

.rc-dialog-body {
  overflow-y: auto;
  padding: 15px
}


